import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuController, ModalController, ToastController } from '@ionic/angular';
import * as moment from 'moment-timezone';
import { UsersDiamanteComponent } from 'src/app/components/users-diamante/users-diamante.component';
import { ApiService } from 'src/app/Services/Api/Api.service';

@Component({
  selector: 'app-asignacion-diamante',
  templateUrl: './asignacion-diamante.page.html',
  styleUrls: ['./asignacion-diamante.page.scss'],
})
export class AsignacionDiamantePage implements OnInit {

  tkn;
  list = [];
  text = '';

  constructor(
    private api: ApiService,
    private toas: ToastController,
    private route: ActivatedRoute,
    private menuCtrl: MenuController,
    private modal:ModalController
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {

    this.menuCtrl.enable(false, 'menu')
    this.route.queryParams.subscribe((param) => {
      console.log(param);
      this.tkn = param.accesstoken;
      this.gestared()
    })

  

    
  }

  gestared() {

    

    this.api.getAssetsGET({
      tkn: this.tkn,
      guid: "1JJkNDXMay"
    }).subscribe((data : any) => {
      this.list = data;
      console.log(data, 'Hola')
    }) 



  }

  async preventivo(item) {
    const modal = await this.modal.create({
      component: UsersDiamanteComponent,
      componentProps: {
        tkn: this.tkn,
        type: 'prev',
        view: 1,
        data: {
          LocationGUID: item.LocationGUID,
          AssetGUID: item.GUID
        }
      }
    })

    await modal.present();

  }


  async correctivo(item) {
    const modal = await this.modal.create({
      component: UsersDiamanteComponent,
      componentProps: {
        tkn: this.tkn,
        type: 'corr',
        view: 1,
        data: {
          LocationGUID: item.LocationGUID,
          AssetGUID: item.GUID
        }
      }
    })

    await modal.present();

  }

  async locs() {
    const modal = await this.modal.create({
      component: UsersDiamanteComponent,
      componentProps: {
        tkn: this.tkn,
        view: 2
      }
    })

    await modal.present();

  }


  filter(event) {
    this.text = event.detail.value;
  }



}
